








import { ADD_CARD } from '@/constants'
import { uuid } from '@/utils'
import { Component, Vue } from 'vue-property-decorator'

@Component
export default class extends Vue {
  private onClick(): void {
    this.$store.dispatch(ADD_CARD, {
      id: uuid(),
      title: 'Default title',
      tasks: []
    })
  }
}
